.Home {
    display: flex;
    margin-top: 1cm;
    column-gap: 3cm;
    justify-content: center;
    align-items: flex-start;

}

.bio-box {
    display: flex;
    justify-content: flex-start;
}

.backI {
    width: 9cm;
    height: auto;
    // position: absolute;
    // z-index: -1;
    // opacity: .8;
    border-radius: 100px;
}

.home-buttons {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: .5cm;
    column-gap: .3cm;
    row-gap: .3cm;
}

.about-button {
    padding: .4cm 1cm;
    border: thin solid rgb(100, 100, 100);
    font-size: 18px;
    border-radius: 500px;
    cursor: pointer;
    background-color: #0f0f13;
    color: white;

    &:hover{
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: rgb(135, 34, 230);
        border:none;
    }
}


.resume-button {
    padding: .4cm 1cm;
    border: thin solid rgb(100, 100, 100);
    font-size: 18px;
    border-radius: 500px;
    cursor: pointer;
    background-color: #0f0f13;
    color: white;
    &:hover{
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: rgb(4, 136, 37);
        border:none;
    }
}

.home-box {
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.bio {
    font-size: 25px;
    font-family: Titillium Web;
    margin-top: -1.2cm;
    align-items: center;
    line-height: .85cm;
    color: rgb(51, 112, 70);
}

.name {
    font-weight: 1000;
    font-size: 100px;
    font-family: changa;
    height: fit-content;
    background: linear-gradient(90deg, rgb(230, 65, 87) 0%, rgb(65, 93, 230) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    margin: 0;
    padding: 0;
    /* Add this line to remove padding */
}